.dp-msmom-notice {

	ul {

		li {
			margin-left: 20px;
			list-style: circle;
		}
	}

	&__link {
		margin-right: 15px;

		&:last-of-type {
			margin-bottom: 17px;
			display: inline-block;
		}

		&#a4r-link-OK {
			margin-bottom: 8px;
			padding-right: 50px;
			padding-left: 50px;
			font-weight: 600;
		}
	}

	border-left-color: rgba(99, 78, 130, 0.9);

	&__button {
		margin-bottom: 17px !important;
		padding: 5px 20px !important;

		&__sub-text {
			margin-top: -8px;
			display: block;
			font-size: 10px;
			font-weight: 400;
			color: #f3dba8;
		}

		&.button-pro-promo {
			font-weight: 600;
			box-shadow: 0 6px 20px -4px rgba(73, 58, 97, 0.71);
			background-color: rgba(99, 78, 130, 0.9);
			text-align: center;
			border: none !important;
			transition: all 0.5s ease;

			&:hover {
				box-shadow: 0 4px 12px -4px rgba(73, 58, 97, 0.51);
				background-color: rgba(99, 78, 130, 1);
			}
		}
	}
}